import React, { Component } from 'react';
import { HorizontalBar } from 'react-chartjs-2';
import { Form, Button, InputGroup } from 'react-bootstrap';
import './Display.css';

class Display extends Component {
  constructor(props) {
    super(props);
    this.state = { selected: [], data: null }; // selected from list

    this.state.propData = this.props.data;
    // { // this.props.data from API
    // {
    //   hospitals: [
    //     { name: "Sutter", charge: 2000000, p_index: 2.0, h_index: 1.0, x: 11, y: -32 }
    //   ]
    // }

    console.log(this.state.propData.Hospitals);
  }

  componentWillMount() {
    this.findNear(this.props.location)
  }

  distance = (lat1, lon1, lat2, lon2) => {
    if ((lat1 === lat2) && (lon1 === lon2)) {
      return 0;
    }
    else {
      let radlat1 = Math.PI * lat1 / 180;
      let radlat2 = Math.PI * lat2 / 180;
      let theta = lon1 - lon2;
      let radtheta = Math.PI * theta / 180;
      let dist = Math.sin(radlat1) * Math.sin(radlat2) + Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta);
      if (dist > 1) {
        dist = 1;
      }
      dist = Math.acos(dist);
      dist = dist * 180 / Math.PI;
      dist = dist * 60 * 1.1515;
      return dist;
    }
  }

  // sorts hospitals by distance
  findNear = coords => {
    console.log(coords)
    if (!coords.coords) { // if didn't use current location
      if (this.state.address) {
        console.log('need to convert')
        // TODO: use google maps to convert address to coordinates and set to coords
      }
      // else { // empty location box
      //   alert("Please type in your address or share Current Location");
      // }
    }

    for (let h of this.state.propData.Hospitals) {
      let dist = this.distance(coords.coords.latitude, coords.coords.longitude, parseFloat(h.x), parseFloat(h.y))
      h.dist = dist;
      // TODO: compare Hospital coordinates with current coordinates
      // push near hospitals to this.state.nearList and remove from this.state.propData.Hospitals
    }
    let hospitals = this.state.propData.Hospitals
    hospitals.sort((a, b) => a.dist - b.dist)
    console.log(hospitals)
    let propData = this.state.propData;
    propData.Hospitals = hospitals
    this.setState({ propData })
  }

  itemClick = (e) => {
    if (e.target.className === 'listItem')
      e.target.className = 'listItemClicked'
    else
      e.target.className = 'listItem'
    console.log(e.target.innerHTML)
    this.chartify(e.target.innerHTML);
  }

  // converts selected data into chart.js friendly structure
  chartify = (clicked) => {
    let old = this.state.selected;
    if (old.indexOf(clicked) !== -1) { // if clicked already in selected tracker
      old.splice(old.indexOf(clicked), 1); // removes from selected tracker

      let data = this.state.data;
      let ind = data.labels.indexOf(clicked);
      data.labels.splice(ind, 1);
      data.datasets[0].data.splice(ind, 1);
      this.setState({ selected: old, data });
    }
    else { // if clicked not yet in chart
      let selected = [...this.state.selected, clicked]
      let data = { // chart data template
        labels: [], datasets: [{
          label: 'Cost in USD ($)',
          backgroundColor: '#38b6ff50',
          borderColor: '#38b6ff',
          borderWidth: 1,
          hoverBackgroundColor: '#38b6ff83',
          hoverBorderColor: '#38b6ff',
          data: []
        }]
      }

      for (let i of selected) {
        data.labels.push(i);
        for (let h of this.state.propData.Hospitals)
          if (h.name === i)
            data.datasets[0].data.push(h.charge);
      }
      this.setState({ data, selected });
    }
  }

  render() {
    return (
      <div className='display'>
        <h1 className='displayTitle'>{this.state.propData.Name}</h1>
        <div className='split'>
          <div>
            <div className='list'>
              <h3 className='displayTitle'>Available Hospitals:</h3>
              {
                this.state.propData.Hospitals.map((h, index) => {
                  return <div key={index}>
                    <ul className='listItem' onClick={(e) => this.itemClick(e)}>{h.name}</ul>
                    <p>Procedure Score: {h.p_index} | Hospital Score: {h.h_index} {h.dist ? `| ${Math.floor(h.dist)} mi.` : null} </p>
                  </div>
                })
              }
            </div>
          </div>
          <div className='chart'>
            {this.state.data === null ? null :
              <HorizontalBar
                data={this.state.data}
                height={600}
                options={{
                  maintainAspectRatio: false, responsiveAnimationDuration: 500,
                  scales: {
                    xAxes: [{
                      ticks: {
                        beginAtZero: true
                      }
                    }]
                  }
                }}
                redraw
              />
            }
          </div>
        </div>
      </div>
    );
  }
}

export default Display;